import "./Dead.css";

function Dead(props) {
    return (
        <div className='dead-container'>
            <div className='dead-role'>
                <img
                    className='dead-card'
                    src={props.player.role.imgUrl}
                    alt=''
                />
                <h2 className='dead-name'>{props.player.role.name}</h2>
                <span
                    className='btn-dead'
                    onClick={() => props.dead(props.player.id)}
                >
                    Dead
                </span>
                <span className='ok-btn-dead' onClick={props.okSeeCard}>
                    OK
                </span>
            </div>
        </div>
    );
}

export default Dead;
