import "./App.css";
import Header from "./header/Header";
import Add from "./add/Add";
import Players from "./players/Players";
import Start from "./start/Start";
import { useState } from "react";
import { Route, Switch, BrowserRouter, Link } from "react-router-dom";
import Show from "./show/Show";
import Cards from "./cards/Cards";
import Roles from "./roles/Roles";
import Timer from "./timer/Timer";
import Dead from "./dead/Dead";
import Footer from "./footer/Footer";

//import img
import detectiveImg from "./assets/img/karagah.jpg";
import doctorImg from "./assets/img/doctor.jpg";
import marshalImg from "./assets/img/kalantar.jpg";
import strongManImg from "./assets/img/Strong.jpg";
import lawyerImg from "./assets/img/vakil.jpg";
import godFatherImg from "./assets/img/God.jpg";
import negotiatorImg from "./assets/img/mozakere.jpg";
import simpleMafiaImg from "./assets/img/mafia.jpg";
import simpleCitizenImg from "./assets/img/shahrvand.jpg";

function App() {
    const [currentPlayer, setCurrentPlayer] = useState({});
    const [show, setShow] = useState(false);
    const [dead, setDead] = useState(false);
    const [continueGame, setContinueGame] = useState(false);
    const [players, setPlayers] = useState([]);
    let [id, setid] = useState(players.length + 1);
    let countPlayers = players.length;
    let countCitizen = Math.floor(countPlayers / 3) * 2 + (countPlayers % 3);
    let countMafia = Math.floor(countPlayers / 3);
    let allRoles = [
        {
            name: "Detective",
            imgUrl: detectiveImg,
            team: 1,
        },
        {
            name: "Doctor",
            imgUrl: doctorImg,
            team: 1,
        },
        {
            name: "Marshal",
            imgUrl: marshalImg,
            team: 1,
        },
        {
            name: "Strong man",
            imgUrl: strongManImg,
            team: 1,
        },
        {
            name: "Lawyer",
            imgUrl: lawyerImg,
            team: 1,
        },
        {
            name: "Godfather",
            imgUrl: godFatherImg,
            team: 2,
        },
        {
            name: "Negotiator",
            imgUrl: negotiatorImg,
            team: 2,
        },
    ];
    for (let index = countMafia - 2; index > 0; index--) {
        allRoles.push({
            name: "Simple mafia",
            imgUrl: simpleMafiaImg,
            team: 2,
        });
    }
    for (let index = countCitizen - 5; index > 0; index--) {
        allRoles.push({
            name: "Simple Citizen",
            imgUrl: simpleCitizenImg,
            team: 1,
        });
    }
    function setDeck() {
        shuffle(allRoles);
        let newPlayers = players.map((player) => {
            return { ...player, role: allRoles.pop() };
        });
        setPlayers(newPlayers);
    }
    function handleSubmit(evt) {
        evt.preventDefault();
        if (evt.target[0].value !== "" && evt.target[0].value !== " ") {
            let newPlayer = {
                id: id,
                name: evt.target[0].value,
                role: undefined,
                show: false,
                dead: false,
            };
            setPlayers([...players, newPlayer]);
            evt.target[0].value = "";
            setid(++id);
        } else {
            alert("name Error");
            evt.target[0].value = "";
        }
    }

    function shuffle(array) {
        var currentIndex = array.length,
            temporaryValue,
            randomIndex;

        // While there remain elements to shuffle...
        while (0 !== currentIndex) {
            // Pick a remaining element...
            randomIndex = Math.floor(Math.random() * currentIndex);
            currentIndex -= 1;

            // And swap it with the current element.
            temporaryValue = array[currentIndex];
            array[currentIndex] = array[randomIndex];
            array[randomIndex] = temporaryValue;
        }

        return array;
    }

    function assignRoles(id) {
        let showPlayer = players.map((player) => {
            return player.id === id ? { ...player, show: true } : player;
        });
        setPlayers(showPlayer);
        let cPlayer = players.filter((player) => {
            return player.id === id;
        });
        let allShow = players.filter((player) => {
            return player.show === false;
        });
        if (allShow.length === 1) {
            setContinueGame(true);
        }
        setCurrentPlayer(cPlayer[0]);
        setShow(true);
    }
    function okSeeCardHandler() {
        setDead(false);
    }
    function deadHandler(id) {
        let deadPlayer = players.map((player) => {
            return player.id === id ? { ...player, dead: true } : player;
        });
        setPlayers(deadPlayer);
        setDead(false);
    }
    function seeCardHandler(id) {
        let cPlayer = players.filter((player) => {
            return player.id === id;
        });
        setCurrentPlayer(cPlayer[0]);
        setDead(true);
    }
    function deletePlayer(id) {
        const newPlayers = players.filter((player) => {
            return player.id !== id;
        });
        setPlayers(newPlayers);
    }
    function okHandler() {
        setShow(false);
    }
    let result;
    if (players.length >= 9) {
        result = <Start setDeck={setDeck} />;
    } else {
        result = (
            <div className='help'>
                <h3
                    className='alert-text'
                    style={players.length < 1 ? { marginTop: "0px" } : {}}
                >
                    Requires at least 9 players!
                </h3>
            </div>
        );
    }
    return (
        <div>
            <BrowserRouter>
                <Header />
                <Switch>
                    <Route exact path='/'>
                        <Add handleSubmit={handleSubmit} players={players} />
                        <Players players={players} delete={deletePlayer} />
                        {result}
                    </Route>
                    <Route
                        exact
                        path='/get-roles'
                        children={() => {
                            "erfan";
                        }}
                    >
                        <Cards players={players} assignRoles={assignRoles} />
                        {show && (
                            <Show
                                okHandler={okHandler}
                                player={currentPlayer}
                            />
                        )}
                        {continueGame && (
                            <div className='manage-wrapper'>
                                <Link
                                    className='manage-btn'
                                    to='/manage-the-game'
                                >
                                    Manage Game
                                </Link>
                            </div>
                        )}
                    </Route>
                    <Route exact path='/manage-the-game'>
                        <Timer />
                        <Roles players={players} seeCard={seeCardHandler} />
                        {dead && (
                            <Dead
                                player={currentPlayer}
                                okSeeCard={okSeeCardHandler}
                                dead={deadHandler}
                            />
                        )}
                        <div className='reset-game-wrapper'>
                            <a href='/' className='reset-game'>
                                New Game
                            </a>
                        </div>
                    </Route>
                </Switch>
            </BrowserRouter>
            <Footer />
        </div>
    );
}
export default App;
