import "./Players.css";
import Player from "./Player";
function Players(props) {
    return (
        <main className='main'>
            <ol className='player-list'>
                {props.players.map((player) => {
                    return (
                        <Player
                            name={player.name}
                            key={player.id}
                            id={player.id}
                            delete={props.delete}
                        />
                    );
                })}
            </ol>
        </main>
    );
}
export default Players;
