import Dead from "../dead/Dead";
import "./Roles.css";

function Roles(props) {
    console.log(props.players);
    return (
        <div className='players-dead'>
            {props.players.map((player) => {
                return (
                    <div
                        className='player-dead'
                        onClick={() => {
                            props.seeCard(player.id);
                        }}
                        style={
                            player.role.team === 1
                                ? { border: "2px solid #05c46b" }
                                : { border: "2px solid #EA2027" }
                        }
                    >
                        {player.name}
                        {player.dead && (
                            <span className='dead-badge'>Dead</span>
                        )}
                    </div>
                );
            })}
        </div>
    );
}

export default Roles;
