import "./Cards.css";
function Card(props) {
    return (
        <>
            <button
                className='card'
                disabled={props.show}
                onClick={() => {
                    props.assignRoles(props.id);
                }}
            >
                <h1 className='card__name'>.: {props.name} :.</h1>
            </button>
        </>
    );
}

export default Card;
