import "./Show.css";

function Show(props) {
    return (
        <div className='show-container'>
            <div className='show-role'>
                <img
                    className='show-card'
                    src={props.player.role.imgUrl}
                    alt=''
                />
                <h2 className='card-name'>{props.player.role.name}</h2>
                <span className='btn-ok' onClick={props.okHandler}>
                    OK
                </span>
            </div>
        </div>
    );
}

export default Show;
