import "./Add.css";

function Add(props) {
    return (
        <aside className='top-aside'>
            <form className='add-form' onSubmit={props.handleSubmit}>
                <input
                    type='text'
                    className='add-from__input'
                    name='name'
                    placeholder='Please Enter Player Name :'
                    autoComplete='off'
                />
                <button type='submit' className='add-form__btn'>
                    Add Player
                </button>
            </form>
        </aside>
    );
}
export default Add;
