import "./Cards.css";
import Card from "./Card";
function Cards(props) {
    return (
        <main className='main-cards'>
            {props.players.map((player) => {
                return (
                    <Card
                        key={player.id}
                        name={player.name}
                        id={player.id}
                        show={player.show}
                        assignRoles={props.assignRoles}
                    />
                );
            })}
        </main>
    );
}

export default Cards;
