import "./Players.css";
import { TiTimesOutline } from "react-icons/ti";

function Player(props) {
    return (
        <li className='player'>
            <span className='player__name'>{props.name}</span>
            <span
                className='player__delete'
                onClick={() => {
                    props.delete(props.id);
                }}
            >
                <TiTimesOutline />
            </span>
        </li>
    );
}
export default Player;
