import "./Timer.css";
import Ding from "./../assets/sound/ding.mp3";
import { useEffect, useState } from "react";
function Timer(props) {
    const [time, setTime] = useState("--");
    const [timeDay, setTimeDay] = useState("--");
    const [timerOn, setTimerOn] = useState(false);
    const [startDay, setStartDay] = useState(false);
    const [bellOn, setBellOn] = useState(false);
    let clear;
    function setTimeHandler(e) {
        setTime(+e.target.value);
        setTimeDay(+e.target.value);
        setStartDay(true);
    }
    function resetDayHandler() {
        clearInterval(clear);
        setStartDay(false);
        setTime("--");
    }
    function startTimeHandler() {
        if (!timerOn) {
            setTimerOn(true);
        } else {
            setTimerOn(false);
        }
    }
    function resetTimeHandler() {
        setBellOn(false);
        clearInterval(clear);
        setTime(timeDay);
        setTimerOn(false);
    }
    function challengeTimeHandler() {
        setBellOn(false);
        clearInterval(clear);
        setTime(Math.ceil(timeDay / 2));
        setTimerOn(false);
    }
    useEffect(() => {
        if (timerOn) {
            if (time === 0) {
                setBellOn(true);
            }
            if (time > 0) {
                clear = setTimeout(() => setTime(time - 1), 1000);
            } else {
                setTimerOn(false);
            }
        } else {
        }
    });
    return (
        <>
            <div className='timer-wrapper'>
                <div className='timer'>{time}</div>
            </div>
            {!startDay && (
                <h1 className='title-timer'>
                    .: Set time for talk for today :.
                </h1>
            )}
            {!startDay && (
                <div className='btns-timer'>
                    <button onClick={setTimeHandler} value='20'>
                        20 Sec
                    </button>
                    <button onClick={setTimeHandler} value='25'>
                        25 Sec
                    </button>
                    <button onClick={setTimeHandler} value='35'>
                        35 Sec
                    </button>
                </div>
            )}
            {bellOn && <audio autoPlay src={Ding}></audio>}
            <h1 className='title-timer'>.: Manage Timer :.</h1>
            <div className='btns-timer'>
                <button onClick={startTimeHandler}>
                    {timerOn ? "Stop" : "Start"}
                </button>
                <button onClick={resetTimeHandler}>Reset</button>
                <button onClick={challengeTimeHandler}>Challenge</button>
            </div>
            {startDay && (
                <div className='day-btn-wrapper'>
                    <button className='reset-day-btn' onClick={resetDayHandler}>
                        Reset Day
                    </button>
                </div>
            )}
        </>
    );
}
export default Timer;
